/* css goes here */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  font-family: "Poppins", sans-serif;
}

section {
  padding: 20px;
}
.nav {
  height: 70px;
  background: #ffffff;
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.1);
  padding: 1.4rem 2.3rem;
  position: sticky;
   top: 0;
   z-index: 1;
}

.nav--logo {
  max-width: 100px;
}

/* hero section */
.hero {
  display: flex;
  flex-direction: column;
  padding: 1.3rem 0 0;
}

.hero-img {
  width: 550px;
  align-self: center;
}

.hero--text {
  text-align: center;
  padding: 1rem 2.25rem;
  font-weight: 600;
  font-size: 2.25rem;
  line-height: 2.5rem;
}

.hero--subtext {
  padding: 0 2.25rem 3rem;
  text-align: center;
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.1rem;
}

@media (max-width: 750px) {
  .hero-img {
    width: 100%;
  }

  .hero--text {
    text-align: left;
  }

  .hero--subtext {
    text-align: left;
    max-width: 400px;
  }
}

/* card component */
.cards-list {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.card {
  font-size: 12px;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  position: relative;
}

.card-badge {
  position: absolute;
  text-transform: uppercase;
    background-color: white;
    top: 6px;
    left: 6px;
    padding:5px 7px ;
    color: black;
    font-weight: bold;
    border-radius: 2px;
}

.card-image {
  width: 200px;
  margin-bottom: 9px;
}

.starImg {
  width: 14px;
  height: 14px;
  margin-right: 3px;
}

.card-stats {
  font-weight: 300;
  font-size: 1rem;
  display: flex;
  align-items: center;
}

.five {
  color: black;
}

.grey {
  color: #918e9b;
  padding: 0 2px;
}

.card-subtext {
  overflow: hidden;
  text-overflow: ellipsis;
}

.card--text {
  font-weight: 600;
  font-size: 1rem;
}

.card--text-span {
  color: #918e9b;
  font-weight: 300;
}

.bold {
  font-weight: bold;
}

@media (max-width: 750px) {
  .cards-list {
    flex-direction: column;
  }

  .starImg {
    width: 12px;
    height: 12px;
  }

  .card-stats {
    font-size: 13px;
  }

  .card-subtext {
    font-size: 12px;
  }

  .card--text {
    font-size: 12px;
  }
}
